.jumboIntro{
    width: 100%;
    display: grid;
    grid-template-columns: 60% 40%;
    grid-template-rows: auto auto;
    height: fit-content;
    min-height: 50vh;
}

.intro_image{
    margin: auto;
    grid-row: 1/3;
    grid-column: 2;
    height: 100%;
    max-height: 50vh;
    width: 100%;
    max-width: 50vh;
}
.intro_title{
    height: fit-content;
    width: 100%;
    grid-row: 1;
    grid-column: 1;
}
.intro_subtitle{
    grid-row: 2;
    grid-column: 1;
    height: fit-content;
}

@media (max-width: 700px){
    .intro_image{
        grid-row: 1;
        grid-column: 2;
    }
    .intro_title{
        grid-row: 1;
        grid-column: 1;
    }
    .intro_subtitle{
        grid-column: 1/3;
        grid-row: 2;
    }
}

@media (max-width: 992px){
    .jumboIntro{
        grid-template-columns: 40% 60%;
    }
}