body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.jumboIntro{
    width: 100%;
    display: grid;
    grid-template-columns: 60% 40%;
    grid-template-rows: auto auto;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    min-height: 50vh;
}

.intro_image{
    margin: auto;
    grid-row: 1/3;
    grid-column: 2;
    height: 100%;
    max-height: 50vh;
    width: 100%;
    max-width: 50vh;
}
.intro_title{
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    width: 100%;
    grid-row: 1;
    grid-column: 1;
}
.intro_subtitle{
    grid-row: 2;
    grid-column: 1;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
}

@media (max-width: 700px){
    .intro_image{
        grid-row: 1;
        grid-column: 2;
    }
    .intro_title{
        grid-row: 1;
        grid-column: 1;
    }
    .intro_subtitle{
        grid-column: 1/3;
        grid-row: 2;
    }
}

@media (max-width: 992px){
    .jumboIntro{
        grid-template-columns: 40% 60%;
    }
}
.aboutContainer{
    display: grid;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    grid-template-rows: auto auto;
}

.aboutTextContainer{
    margin: 2%;
}

.skillsCardsContainer{
}
.skillsCard{
    margin-bottom: 1%;
}

.projectCardContainer{
    min-height: 30vh;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    width: 30vh;
    padding: 1%;
}

.projectCardContainer:hover{
    background-color: #e6e6e6;
}

.projectCardColumn{
}

@media (max-width: 700px){
    .projectCardContainer{
        width: 100%;
    }
}
.workContainer{
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
}
.workCell{
    padding-top: 2%;
}
.contactContainer{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center
}

#emailButton{
    width: 25vh;
}
.socialMedia img{
    height: 10vh;
    width: 10vh;
}
.chapterContainer{
    margin-left: 3%;
    margin-right: 3%;
    margin-bottom: 1%;
    padding: 2%;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
}
.chapterTitle{
    padding-top: 2%;
}
